rflow: hidden;\n  text-overflow: ellipsis;\n  display: -webkit-box;\n  -webkit-line-clamp: 3; /* number of lines to show */\n  -webkit-box-orient: vertical;\n}\n\n.card-item__article-detail {\n  margin: 60px auto;\n  display: flex;\n  flex-direction: column;\n}\n\n.card-item__article-detail h2 {\n  margin-bottom: 40px;\n  font-size: 25px;\n  text-align: center;\n}\n\n.card-item__article-detail img {\n  margin: auto;\n  font-size: 30px;\n  width: 100%;\n}\n\n.card-item__article-detail .article-info {\n  font-size: 13px;\n  margin-bottom: 40px;\n}\n\n.card-item__article-detail .article-info a {\n  text-decoration: none;\n  color: #ef4651;\n}\n\n/* ================================ footer ================================ */\n.my-footer {\n  display: flex;\n  width: 100%;\n  padding: 20px 130px;\n  text-align: center;\n  background-color: #ef4651;\n  color: rgb(255, 255, 255);\n  box-shadow: 0 -4px 8px 0 rgba(0, 0, 0, 0.2);\n}\n\n.footer_sosmed img{\n  padding: 8px;\n}\n\n.footer_brand {\n  text-align: center;\n}\n\n.footer_brand h1{\n  letter-spacing: 5px;\n}\n\n.footer_info {\n  padding-top: 10px;\n  display: grid;\n  grid-template-columns: repeat(3, 1fr);\n  text-align: center;\n  flex-grow: 2;\n}\n\n.footer_info-list {\n  display: flex;\n}\n\n/* ================================ Games ================================ */\n\n.games button {\n  border: none;\n  background-color: white;\n  cursor: pointer;\n}\n\n#game, #gameMath {\n  width: 660px;\n  height: 570px;\n  margin: 20px auto;\n  display: flex;\n  flex-wrap: wrap;\n}\n\n#gameMath {\n  width: 800px;\n  height: auto;\n  justify-content: center;\n}\n\n.memory-card {\n  width: calc(25% - 10px);\n  height: calc(33.333% - 10px);\n  margin: 5px;\n  position: relative;\n  transform: scale(1);\n  transform-style: preserve-3d;\n  transition: transform .5s;\n}\n\n.flip {\n  transform: rotateY(180deg);\n}\n\n.front-face, .back-face {\n  width: 100%;\n  height: 100%;\n  position: absolute;\n  border-radius: 5px;\n  background: #c4e3ef;\n  backface-visibility: hidden;\n}\n\n.back-face {\n  padding: 60px 20px;\n}\n\n.front-face {\n  padding: 20px;\n  transform: rotateY(180deg);\n}\n\n#timer, #reset {\n  text-align: center;\n  margin-right: 20px;\n}\n\n#reset, #backToMenu, .game-item {\n  background-color: #f44336;\n  border: none;\n  color: white;\n  padding: 15px 32px;\n  text-align: center;\n  text-decoration: none;\n  font-size: 16px;\n  cursor: pointer;\n}\n\n#backToMenu {\n  background-color: #4CAF50;\n}\n\n.game-item {\n  background-color: #4CAF50;\n  margin: 20px 30vw 20px;\n}\n\n.gameOption {\n  display: flex;\n  flex-direction: row;\n  justify-content: center;\n  align-items: center;\n  margin: 10px 0 35px;\n}\n\n.firstSection, .secondSection {\n  width: 220px;\n  display: flex;\n  align-content: center;\n  justify-content: right;\n  flex-wrap: wrap;\n}\n\n.secondSection {\n  justify-content: left;\n}\n\n.firstNumber, .secondNumber {\n  width: 70px;\n}\n\n.symbol {\n  width: 50px;\n  margin: 0 40px;\n}\n\n#inputNumber {\n  width: 100px;\n  text-align: center;\n  margin: 10px;\n  font-size: 30px;\n  height: 40px;\n}\n\n#cekInputNumber {\n  border: none;\n  color: white;\n  padding: 15px 32px;\n  text-align: center;\n  text-decoration: none;\n  font-size: 16px;\n  cursor: pointer;\n  background-color: #4CAF50;\n}\n\n#gameMath h2 {\n  text-align: center;\n  flex: 0 0 100%;\n  margin: 50px 0 20px;\n}\n\n.inputNumberSection {\n  text-align: center;\n  height: 320px;\n}\n\n.jawabSalah {\n  color: red;\n  display: inline;\n  margin-left: 20px;\n}\n\n.gameSelesai {\n  margin-top: 10px;\n  color: green;\n}\n\n.addFruit, .minusFruit {\n  margin: 10px;\n  cursor: pointer;\n}\n\n#belanjaBox {\n  display: flex;\n  align-content: center;\n  justify-content: center;\n  border-style: dashed;\n  width: 370px;\n  height: 310px;\n  flex-wrap: wrap;\n}\n\n#gameMath h3 {\n  text-align: center;\n  flex: 0 0 100%;\n  padding: 30px;\n}\n\n.checkSection {\n  width: 460px;\n  height: 200px;\n  text-align: center;\n}\n"],"sourceRoot":""}]);
